import React from 'react'
import "./SkillBar.css"
import styled from 'styled-components'

function SkillBar({heading, percent}) {

    const Progress = styled.div`
        background-color: var(--custom);
        height: 100%;
        width: ${percent};
        border: 1px solid #242124;
        border-radius: 10px;
    `;

    return (
        <div className="bar">
            <div className="bar__top">
                <h5>{heading}</h5>
                <span>{percent}</span>
            </div>
            <div className="bar__bottom">
                <Progress />
            </div>
        </div>
    )
}

export default SkillBar
