import { motion } from 'framer-motion'
import React from 'react'
import About from './About'
import Fact from './Fact'
import "./Main.css"


function Main() {
    return (
        <motion.div className="main" initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}}>
            <About />
            <Fact />
        </motion.div>
    )
}

export default Main
