import React from 'react'
import "./PortfolioModal.css"
import CancelIcon from '@material-ui/icons/Cancel';

function PortfolioModal({showModal, setShowModal, url}) {
    return (
        <>
        {showModal &&
        <div className="portfolioModal">
            {/* iframe can be used for both external url or video from internal folders */}
            <iframe src={url} frameborder="0" title="video"></iframe> 
            <button onClick={() => setShowModal(prev => !prev)}><CancelIcon /></button>
        </div>
        }
        </>
    )
}

export default PortfolioModal


