import { motion } from 'framer-motion'
import React from 'react'
import Heading from './Heading'
import "./Resume.css"
import ResumeItem from './ResumeItem'
import SkillBar from './SkillBar'

function Resume() {
    return (
        <motion.div className="resume" initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}}>
            <Heading 
                heading="Resume"
                text="3 Years of Experience"
            />
            <div className="resume__content">
                <div className="resume__content__left">
                    <h3>Education</h3>
                    <div className="resume__content__leftDiv">
                        <ResumeItem 
                            year="2017-2019"
                            p="University of Economics, Prague"
                            heading="Information Management"
                            text="Main focus of the master program Information Management: Managing informatics in organizations, with an emphasis on the security of information systems. The increased emphasis on the audid of the corporate information system and issues of its security. The study program uses trans- and multidisciplinary knowledge and skills in the fields of systems science, business informatics and management."
                        />
                        <ResumeItem 
                            year="2014-2017"
                            p="University of Economics, Prague"
                            heading="Applied Informatics"
                            text="Main focus of the bachelor program Applied Informatics: Designing and implementing information systems in organizations, analyzing and practically designing databases, using software engineering and processing information for business developemnt. In addition, gaining economic knowledge and the ability to manage projects and teams."
                        />
                    </div>
                </div>
                <div className="resume__content__right">
                    <h3>Experience</h3>
                    <div className="resume__content__rightDiv">
                        <ResumeItem 
                            year="2019 - Current"
                            p="Deutsche Telekom IT Solutions Slovakia"
                            heading="Application Developer"
                            text="Application developer with the focus on database development."
                        />
                        <ResumeItem 
                            year="2017-2019"
                            p="Deutsche Boerse, Prague"
                            heading="Data Master"
                            text="Data master with the focus on working with SAP."
                        />
                    </div>
                </div>
            </div>

            <div className="skills__content">
                <div className="skills__content__left">
                <h3>Coding Skills</h3>
                    <div className="skills__content__leftDiv">
                        <SkillBar 
                            heading="HTML / CSS"
                            percent="100%"
                        />
                        <SkillBar 
                            heading="SQL"
                            percent="90%"
                        />
                        <SkillBar 
                            heading="Javascript"
                            percent="70%"
                        />
                        <SkillBar 
                            heading="React"
                            percent="70%"
                        />
                    </div>
                </div>
                    
                <div className="skills_content__right">
                    <h3>Design Skills</h3>
                    <div className="skills__content__rightDiv">
                        <SkillBar 
                            heading="Adobe Photoshop"
                            percent="50%"
                        />
                        <SkillBar 
                            heading="Adobe Illustrator"
                            percent="65%"
                        />
                    </div>
                </div>
            </div>
        </motion.div>
    )
}

export default Resume
