import React from 'react'
import "./Fact.css"
import FactItem from './FactItem'
import DoneIcon from '@material-ui/icons/Done';

function Fact() {
    return (
        <div className="fact">
            <h3>What I Offer</h3>
            <div className="fact__info">
                <FactItem 
                    icon={<DoneIcon />}
                    heading="Design"
                />
                <FactItem 
                    icon={<DoneIcon />}
                    heading="Portfolio Development"
                />
                <FactItem 
                    icon={<DoneIcon />}
                    heading="Ecommerce Development"
                />
                <FactItem 
                    icon={<DoneIcon />}
                    heading="Application Development"
                />
            </div>
        </div>
    )
}

export default Fact
