import React from 'react'
import "./WorkFocus.css"

function WorkFocus({icon, heading, text}) {
    return (
        <div className="work">
            <div className="work__icon">
                {icon}
            </div>
            <div className="work__content">
                <h4>{heading}</h4>
                <p>{text}</p>
            </div>
        </div>
    )
}

export default WorkFocus
