import React from 'react'
import Button from './Button'
import "./Intro.css"
import img from "../img/me.jpeg"
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'

function Intro() {
    return (
        <motion.div className="intro" initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}}>
            <div className="intro__left">
                <div className="intro__left__outer">
                    <div className="intro__left__inner">
                        <img src={img} alt="me"></img>
                    </div>
                </div>
            </div>
            <div className="intro__right">
                <span>Frontend-developer</span>
                <h1>Victoria Štefániková</h1>
                <p>Welcome to my personal portfolio web. My name is Victoria and I am a passionate developer. I am working as a freelancer. I give myself a mission to 
                   produce a creative websites and applications with beautiful details. I enrich the design with my own illustrations and 
                   designs that are created with precision and based on your desire.
                </p>
                <div className="intro__right__btns">
                    <Link className="intro__link" to="/contact">
                        <Button 
                            color="#0080ff"
                            text="Contact"
                        />
                    </Link>
                </div>
                
            </div>
        </motion.div>
    )
}

export default Intro
