import React from 'react'
import "./About.css"
import WorkFocus from './WorkFocus'
import BrushIcon from '@material-ui/icons/Brush';
import ImportantDevicesIcon from '@material-ui/icons/ImportantDevices';
import StorefrontIcon from '@material-ui/icons/Storefront';
import AppsIcon from '@material-ui/icons/Apps';

function About() {
    return (
        <div className="about">
            <h3>What I Do</h3>
            <div className="about__info">
                <WorkFocus 
                    icon={<BrushIcon />}
                    heading="Design"
                    text="Designing a product is my favourite activity. I like to explore and get inspired, but the final result of my work is a connection between inspiration and my ideas and imagination."
                />

                <WorkFocus 
                    icon={<ImportantDevicesIcon />}
                    heading="Development"
                    text="When developing a product, it is especially important to create a reusable components which results in clarity and easier understanding of the code. The source that I use for this purpose is REACT."
                />

                <WorkFocus 
                    icon={<StorefrontIcon />}
                    heading="Ecommerce"
                    text="For me, creating ecommerce applications is a kind of an extension of the presentation. However, not a presentation of a person in the form of a static website, but a product where I combine design, functionality and the ability to interact with the content of the application."
                />

                <WorkFocus 
                    icon={<AppsIcon />}
                    heading="App / Portfolio"
                    text="My development beginnings lie in the creation of web portfolios. A portfolio website is the quickest and easiest way of showcasing your work as a designer or freelancer. Building an apps has became both challenge and joy. The diversity of application development mainly helps me to build up on my functional skills."
                />
            </div>
        </div>
    )
}

export default About
