import React, { useState } from 'react'
import "./Contact.css"
import ContactItem from './ContactItem'
import Heading from './Heading'
import PlaceIcon from '@material-ui/icons/Place';
import EmailIcon from '@material-ui/icons/Email';
import Button from './Button';
import { motion } from 'framer-motion';
import emailjs from "emailjs-com";

function Contact() {

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [subject, setSubject] = useState('');
    const [message, setMessage] = useState('');

    const submit = (e) => {
        e.preventDefault();
    }    

    const sendEmail = () => {
        let data = {
            name: name,
            email: email,
            subject: subject,
            message: message,
        };
        if (name && email &&  message) {
            emailjs.send('service_fxfjsf7', 'template_wj4qlga', data, 'user_BcHQmiHugqEno43ZAr54G')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        };
        setName('');
        setEmail('');
        setSubject('');
        setMessage('');
    }

    return (
        <motion.div className="contact" initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}}>
            <Heading 
                heading="Contact"
                text="Get in Touch"
            />
            <div className="contact__content">
                <div className="contact__contentLeft">
                    <ContactItem 
                        icon={<PlaceIcon />}
                        heading="Slovakia"
                        text=""
                    />
                    <ContactItem 
                        icon={<EmailIcon />}
                        heading="victoria.stefanikova@gmail.com"
                        text="Interested in my work? Leave a message or contact me for further info."
                    />
                </div>
                <div className="contact__contentRight">
                    <h3>How Can I Help You?</h3>
                    <form onSubmit={submit}>
                        <div className="form__left">
                            <input placeholder="Full Name" className="form__input" name="name" value={name} onChange={(e) => setName(e.target.value)}></input>
                            <input placeholder="Email Address" className="form__input" name="email" required value={email} onChange={(e) => setEmail(e.target.value)}></input>
                            <input placeholder="Subject" className="form__input" name="subject" value={subject} onChange={(e) => setSubject(e.target.value)}></input>
                        </div>
                        <div className="form__right">
                            <textarea placeholder="Message" name="message" required value={message} onChange={(e) => setMessage(e.target.value)}></textarea>
                        </div>
                    </form>
                    <Button 
                        color="#0080ff"
                        text="Send message"
                        type="submit"
                        click={sendEmail}
                    />
                </div>
            </div>
        </motion.div>
    )
}

export default Contact
