import React, { useState } from 'react'
import Heading from './Heading'
import "./Portfolio.css"
import PortfolioItem from './PortfolioItem'
import FlipMove from "react-flip-move";
import data from "./data"
import { motion } from 'framer-motion';

function Portfolio() {

    const [portfolio, setPortfolio] = useState(data); 
    const [activePortfolio, setActivePortfolio] = useState();

    const click = (e) => {
        handleClick(e);
        toggleActive(e);
    }

    const handleClick = (e) => {
        if(e.target.innerHTML === "All") {
            setPortfolio(data);
        }
        else {
            setPortfolio(data.filter(item => item.cat === e.target.innerHTML));
        }
    }

    const toggleActive = (e) => {
        setActivePortfolio(e.target.innerHTML);
    }

    return (
        <motion.div className="portfolio" initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}}>
            <Heading 
                heading="Portfolio"
                text="My work"
            />
            <div className="portfolio__content">
                <div className="portfolio__nav">
                    <ul>
                        <li className={activePortfolio === "All" ? "active" : ""} onClick={click}>All</li>
                        <li className={activePortfolio === "Website" ? "active" : ""} onClick={click}>Website</li>
                        <li className={activePortfolio === "Application" ? "active" : ""} onClick={click}>Application</li>
                    </ul>
                </div>
                <FlipMove className="portfolio__main">
                    {portfolio.map((data) => (
                        <PortfolioItem 
                            id={data.id}
                            src={data.src}
                            heading={data.heading}
                            url={data.video}
                        />
                    ))}
                </FlipMove>
            </div>
        </motion.div>
    )
}

export default Portfolio
