import React from 'react'
import "./ResumeItem.css"

function ResumeItem({year, p, heading, text}) {
    return (
        <div className="resumeItem">
            <div className="resumeItem__top">
                <span>{year}</span>
                <p>{p}</p>
            </div>
            <div className="resumeItem__bottom">
                <h4>{heading}</h4>
                <p>{text}</p>
            </div>
        </div>
    )
}

export default ResumeItem
