import React, { useState } from 'react'
import "./Navbar.css"
import CloseIcon from '@material-ui/icons/Close';
import MenuIcon from '@material-ui/icons/Menu';
import { Link, NavLink } from 'react-router-dom';

function Navbar() {

    const [clicked, setClicked] = useState(false);

    const handleClick = () => setClicked(!clicked);

    const closeMobileMenu = () => setClicked(false);


    return (
        <div className="nav">
            <div className="nav__left">
                <Link className="span" to="/"><span>V</span></Link> 
                <h3>Victoria Štefániková</h3>
            </div>
            <div className={`nav__right ${clicked ? "nav__menu active" : "nav__menu"}`}>
                <ul>
                    <NavLink className="li" activeClassName="active" exact path to="/" onClick={closeMobileMenu}><li>About Me</li></NavLink> 
                    <NavLink className="li" activeClassName="active" to="/resume" onClick={closeMobileMenu}><li>Resume</li></NavLink>
                    <NavLink className="li" activeClassName="active" to="/portfolio" onClick={closeMobileMenu}><li>Portfolio</li></NavLink>
                    <NavLink className="li" activeClassName="active" to="/contact" onClick={closeMobileMenu}><li>Contact</li></NavLink>   
                </ul>
            </div>
            <div className="nav__icon" onClick={handleClick}>
                {clicked ? <CloseIcon /> : <MenuIcon />}
            </div>
        </div>
    )
}

export default Navbar
